.header-home {
	margin: 32px 20px;
	height: calc(100vh - 64px);
	border-radius: 20px;
}
.header-home .carousel-item img {
	height: calc(100vh - 64px);
	border-radius: 20px;
}
@media only screen and (max-width: 500px) {
	.header-home {
		margin: 16px 8px !important;
	}
	.header-home,
	.header-home .carousel-item img {
		height: 50vh;
	}
}
