.navbar {
	background-color: #fff;
	position: absolute;
	padding: 0 2rem;
	left: 60px;
	right: 60px;
	width: calc(100% - 120px);
	z-index: 999;
	height: 95px;
	border-radius: 50px;
	box-shadow: 0px 0px 12px 0px #00000026;
}
.navbar a {
	color: #000;
	font-size: 18px;
	font-weight: 600;
	padding: 6px 20px;
	line-height: normal;
	outline: none;
}
.navbar a:hover {
	color: #555;
}
.navbar a.active {
	color: #2d9495 !important;
}
.navbar-collapse {
	justify-content: flex-end;
}
.navbar .cart-num {
	background-color: #2d9495;
	color: #fff;
	position: absolute;
	border-radius: 50%;
	right: -12px;
	top: -12px;
	padding: 0 6px;
	font-size: 14px;
}
.nav-mobile-logo {
	display: none;
}
.navbar-toggler-icon {
	background-image: url("../../assets/hamburger.svg") !important;
	border-color: var(--primary-color);
}
.web-view {
	display: block;
}
.mobile-view {
	display: none;
}
.navbar-nav {
	display: flex;
	gap: 24px;
}
@media only screen and (max-width: 500px) {
	.navbar {
		left: 20px;
		right: 20px;
		width: calc(100% - 40px);
		padding: 0 1rem;
		top: 30px !important;
		height: 70px;
		box-shadow: 0 0 4px #dedede;
	}
	.navbar-nav {
		padding: 16px 0;
		box-shadow: 0 0 4px #dedede;
		gap: 12px;
	}
	.web-view {
		display: none !important;
	}
	.mobile-view {
		display: block;
	}
	.navbar-toggler {
		padding: 4px 6px;
	}
	.navbar .col-md-5 {
		padding: 0;
	}
	.navbar:has(.show) {
		border-radius: 15px 15px 0 0;
		transition: all 0.2s ease-out;
	}
	.navbar-collapse {
		position: absolute;
		background: #fff;
		top: 70px;
		left: 0;
		width: 100% !important;
		border-radius: 0 0 8px 8px;
	}
	.navbar-collapse a {
		margin-bottom: 8px;
	}
	.nav-logo {
		display: none;
	}
	.nav-mobile-logo {
		display: block;
	}
}
