footer {
	background: linear-gradient(103.47deg, #2d9495 1.25%, #254373 95.45%);
	color: #fff;
}
footer .links a {
	display: block;
}
footer a {
	color: #fff;
	text-decoration: none;
	font-size: 16px;
	margin-bottom: 8px;
	font-weight: 700;
}
footer .three div {
	display: flex;
	align-items: center;
	gap: 12px;
	margin-bottom: 16px;
}
.apps .icons p {
	margin-bottom: 8px;
}
.apps .icons i {
	border: 1px solid;
	border-radius: 8px;
	padding: 4px;
}
.apps a {
	font-size: 14px;
	font-weight: 500;
	color: #fff;
	text-decoration: none;
	margin-right: 12px;
}
.social .icons img {
	height: 18px;
}
.social .visa img {
	width: 37px;
	height: 25px;
}
footer .icons {
	display: flex;
	gap: 16px;
}
.social .icons a {
	margin-left: 8px;
	padding: 7px;
	border-radius: 5px;
}
footer .power {
	color: #fff;
	font-weight: 700;
	margin: 0;
}
footer .power a {
	color: #fff;
	text-decoration: none;
}
.ar footer .qr {
	text-align: left;
}
.en footer .qr {
	text-align: right;
}
footer .qr div {
	justify-content: end;
}
@media only screen and (max-width: 500px) {
	.mt-5 {
		margin-top: 1rem !important;
	}
	footer {
		text-align: center;
	}
	.col-12 {
		margin-bottom: 2rem;
	}
	.col-12:nth-child(2) {
		margin-bottom: 0;
	}
	.ar footer .qr,
	.en footer .qr {
		text-align: center;
	}
	.qr {
		display: none;
	}
	footer .qr div {
		justify-content: center;
	}
	footer .icons {
		justify-content: center;
	}
	.custom-div {
		padding: 2rem 0 0 !important;
	}
	.power-sec {
		flex-direction: column;
	}
	.power-sec div {
		justify-content: center !important;
		margin-top: 8px;
	}
	.three {
		margin-bottom: 0;
	}
	footer .three div {
		margin-bottom: 12px;
	}
	footer .three svg {
		width: 32px;
		height: 32px;
	}
	footer .three div:last-of-type {
		margin-bottom: 0;
	}
}
