.contact {
	/* padding: 1rem 0; */
}
.contact .banner {
	margin: 20px;
	border-radius: 40px;
	height: 500px;
	background-size: cover;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: linear-gradient(0deg, #ffffff, #ffffff),
		linear-gradient(0deg, #20395d, #20395d),
		linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	/* background: linear-gradient(0deg, #ffffff, #ffffff),
		linear-gradient(0deg, #20395d, #20395d),
		linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
 	*/
}
.contact .send {
	background-color: #2d9495;
	color: #fff;
	border-radius: 8px;
	width: 100%;
	height: 40px;
}
.contact .contact-whats {
	color: var(--primary-color);
	background-color: #fff;
	border-radius: 8px;
}
.getInTouch {
	padding: 2rem;
	border: 1px solid #d3cdcd;
	border-radius: 20px;
}
.getInTouch h4 {
	font-size: 24px;
	margin-bottom: 24px;
	font-weight: 700;
}
.getInTouch label {
	font-size: 14px;
	font-weight: 700;
}
