.products-home h1 {
	font-weight: 700;
}
.products-home {
	background: #dcf0f0;
	text-align: center;
	margin-top: 3rem !important;
	position: relative;
	padding: 75px 2rem;
}

.products-home .tabs {
	background: #fff;
	border-radius: 50px;
	width: max-content;
	padding: 32px 16px;
	margin: 34px auto 60px;
}
.products-home .tabs span {
	padding: 18px 60px;
	border-radius: 30px;
	cursor: pointer;
	font-weight: 700;
}
.products-home .tabs span.active {
	background: #2d9495;
	color: #fff;
}
.products-home .products {
	width: 80%;
	margin: auto;
}
.products-home .product-card {
	position: relative;
	border-radius: 18px;
	overflow: hidden;
	background: #fff;
	text-align: start;
}
.products-home .product-card img {
	height: 250px;
	width: 100%;
}
.products-home .product-card .product-body {
	padding: 16px;
}
.products-home .product-card .product-title {
	font-size: 24px;
	margin-top: 12px;
	margin-bottom: 12px;
	cursor: pointer;
	display: block;
	color: #000;
	text-decoration: none;
}
.products-home .product-card .price {
	font-size: 18px;
	color: #2d9495;
	margin-bottom: 12px;
}
.products-home .product-card button {
	height: 40px;
	background: #2d9495;
	border-radius: 8px;
	width: 100%;
	line-height: 1;
	font-weight: 700;
}
.products-home .product-card .old-price {
	margin: 0 12px;
}
.products-home .product-card .tag {
	background: #dcf0f0;
	padding: 4px 22px;
	border-radius: 50px;
}
.products-home .product h4 {
	position: absolute;
	left: 50%;
	bottom: 7%;
	transform: translate(-50%, 6px);
}

.products-home .viewAll {
	padding: 8px 20px;
	background-color: var(--primary-color);
	color: #ffffff;
	border-radius: 5px;
	text-decoration: none;
}

.products-home .cate {
	cursor: pointer;
	overflow: hidden;
	box-shadow: 0 0 5px #dedede7a;
	min-height: 326px !important;
	height: 326px !important;
}
.products-home .cate .desc {
	position: absolute;
	background-color: #0e3a5d78;
	top: 50%;
	left: 50%;
	max-width: 90%;
	color: #ffffff;
	transform: translate(-50%, -50%);
	padding: 2rem;
	border-radius: 5px;
	text-align: center;
	text-wrap: nowrap;
}
.products-home .cate:hover .desc {
	background-color: var(--primary-color);
	transition: 0.5s ease-in-out;
}

.products-home .cate:hover img {
	transform: scale(1.1);
	transition: transform 0.5s ease-in-out;
}
.products-home .desc p {
	font-size: 26px;
}
.products-home .desc a {
	background: none;
	font-size: 16px;
	color: #ffffff;
}
@media only screen and (max-width: 500px) {
	.products-home {
		margin-top: 1rem !important;
		padding: 1rem;
	}
	.products-home h1 {
		margin-bottom: 1rem;
		font-size: 24px;
	}
	.products-home h1::after {
		content: "";
	}
	.products-home .cate {
		min-height: 250px !important;
		height: 250px !important;
	}
	.products-home .desc p {
		font-size: 20px;
		margin-bottom: 4px;
	}
	.products-home .tabs {
		padding: 20px 12px;
	}
	.products-home .tabs span {
		padding: 12px 16px;
	}
}
