.banner {
	background: linear-gradient(103.47deg, #2d9495 1.25%, #254373 95.45%);
	padding: 70px;
	border-radius: 40px;
	color: #fff;
}
.banner .sub-title {
	text-transform: uppercase;
	font-weight: 300;
}
.banner .title {
	font-size: 48px;
	font-weight: 700;
}
.banner .numbers {
	color: #d9d9d9;
	font-weight: 700;
	font-size: 80px;
}
.banner .download-pdf {
	background: #fff;
	text-decoration: none;
	color: var(--primary-color);
	border-radius: 40px 5px;
	padding: 22px 70px;
	font-weight: 700;
	display: inline-block;
}
.banner img {
	height: 230px;
	border-radius: 200px;
}
.banner .viewAll {
	background-color: var(--primary-color);
	text-decoration: none;
	color: #fff;
	margin-top: 12px;
	width: fit-content;
	padding: 9px 22px;
	border-radius: 8px;
}
@media only screen and (max-width: 500px) {
	.banner {
		padding: 1rem;
		margin-bottom: 1rem;
	}
	.banner h1 {
		margin-bottom: 2rem;
		font-size: 22px;
	}
	.banner h1::after {
		content: "";
	}
	.banner img {
		margin-bottom: 2rem;
	}
	.banner .title {
		font-size: 28px;
	}
	.banner .numbers {
		font-size: 40px;
	}
	.banner button {
		padding: 18px 50px;
	}
	.banner img {
		height: 150px;
		width: 100% !important;
		border-radius: 30px;
	}
}
