.about-home {
	margin-top: 3rem;
}
.about-home .tag {
	font-size: 32px;
	text-decoration: underline;
	font-weight: 400;
}
.about-home .title {
	font-size: 60px;
	font-weight: 700;
	line-height: 1;
}

.about-home .description {
	color: #797b78;
}
.about-home img {
	border-radius: 25px;
	height: 100%;
}
@media only screen and (max-width: 500px) {
	.about-home .tag {
		font-size: 18px;
	}
	.about-home .title {
		font-size: 32px;
	}
}
