.main-page .banner {
	margin: 20px;
	border-radius: 40px;
	height: 500px;
	background-size: cover;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: linear-gradient(0deg, #ffffff, #ffffff),
		linear-gradient(0deg, #20395d, #20395d),
		linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	/* background: linear-gradient(0deg, #ffffff, #ffffff),
		linear-gradient(0deg, #20395d, #20395d),
		linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
 	*/
}
.main-page .static {
	padding: 72px 0 0;
	margin: 40px 32px;
	background: #dcf0f0;
	border-radius: 20px;
}
.static .about {
	padding: 40px;
	border-radius: 10px;
}

.static .about .about-card {
	position: relative;
}
.static .about .about-card img {
	width: 420px;
	height: 500px;
	border-radius: 10px;
}
.static .about .about-card .details {
	background-color: #fff;
	border: 1px solid #e1e1e1;
	border-radius: 20px;
	padding: 35px;
	width: 75%;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
}
.static .about .about-card.one .details {
	right: 0;
}
.static .about .about-card.two {
	text-align: end;
}
.static .about .about-card.two .details {
	left: 0;
	text-align: start;
	max-height: 90%;
}
.static .about .about-card .details .details-title {
	font-weight: 700;
	font-style: Bold;
	font-size: 40px;
	line-height: 100%;
	color: #404040;
	margin-bottom: 40px;
}
.static .about .about-card .details .details-description {
	font-size: 16px;
	color: #62636c;
	line-height: 24px;
}
.ar .static .about .about-card.one .details {
	right: auto;
	left: 0;
}
.ar .static .about .about-card.two .details {
	right: 0;
	left: auto;
}
@media only screen and (max-width: 900px) {
	.static .about {
		padding: 20px 8px;
	}
	.static .about .about-card {
		display: flex;
		flex-direction: column;
	}
	.static .about .about-card img {
		width: 100%;
		height: 200px;
		order: 1;
		padding: 6px;
		border-radius: 8px;
	}
	.static .about .about-card .details {
		position: initial;
		transform: none;
		width: 100%;
		order: 2;
		margin-top: 16px;
		padding: 18px;
	}
	.static .about .about-card .details .details-title {
		font-size: 28px;
		margin-bottom: 20px;
	}
	.static .about .about-card .details .details-description {
		line-height: 22px;
		margin: 0;
	}
	.static .about .about-card.two .details {
		height: 100%;
	}
	.main-page .banner {
		height: 230px;
		justify-content: end;
		margin: 20px 10px;
	}
}
