/* General Styles */
body {
	margin: 0;
	background-color: #fff;
	overflow-x: hidden;
}
@font-face {
	font-family: "Almarai-Bold";
	src: url("../src/common/Fonts/Almarai-Bold.ttf") format("truetype");
	font-weight: normal;
	font-style: normal;
}
.en {
	font-family: "Exo", sans-serif;
	direction: ltr;
}
.ar {
	font-family: "Almarai-Bold";
	direction: rtl;
	font-optical-sizing: auto;
}
.custom-div {
	/* background-color: var(--primary-color); */
	padding: 3rem 2rem 0;
	color: #ffffff;
}

.custom-div h1 {
	margin-bottom: 0;
	font-weight: 600;
}

button {
	background-color: black;
	color: white;
	padding: 10px 20px;
	border: none;
	cursor: pointer;
}

/* Header Styles */
header {
	background-color: white;
	padding: 1rem 2rem;
}

header nav ul {
	list-style: none;
	display: flex;
	justify-content: space-around;
	margin: 0;
	padding: 0;
}

header nav ul li {
	margin: 0 1rem;
	color: black;
	cursor: pointer;
}

/* Featured Products Styles */
.featured-products {
	background-color: #2e7a9d;
	padding: 2rem;
}

.featured-products h2 {
	margin-bottom: 1rem;
}

.featured-products .products {
	display: flex;
	justify-content: center;
	gap: 1rem;
}

.featured-products .products .product {
	background-color: white;
	color: black;
	padding: 1rem;
	border-radius: 10px;
	text-align: center;
}

/* Best Selling Products Styles */
.best-selling-products {
	background-color: #3e9abd;
	padding: 2rem;
}

.best-selling-products h2 {
	margin-bottom: 1rem;
}

.best-selling-products .products {
	display: flex;
	justify-content: center;
	gap: 1rem;
}

.best-selling-products .products .product {
	background-color: white;
	color: black;
	padding: 1rem;
	border-radius: 10px;
	text-align: center;
}

/* Download Section Styles */
.download-section {
	background-color: #4ebadc;
	padding: 2rem;
}

.download-section h2 {
	margin-bottom: 1rem;
}

.download-section button {
	margin-top: 10px;
}

/* Footer Styles */
footer {
	background-color: #0e3a5d;
	padding: 2rem 2rem 1rem;
}

.footer-content {
	display: flex;
	justify-content: space-between;
}

.footer-content .quick-links ul {
	list-style: none;
	padding: 0;
}

.footer-content .quick-links ul li {
	margin: 0.5rem 0;
}

.footer-content .social-media,
.footer-content .qr-code {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.btn-primary {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
}
.btn-primary:hover {
	opacity: 0.8;
	background-color: var(--primary-color);
}
.paths {
	padding: 20px 0;
	margin-bottom: 30px;
}
.paths a {
	background: none !important;
	color: #333 !important;
	padding: 0 !important;
	text-decoration: none;
}
.paths .gt {
	margin: 0 6px;
}
.paths h1 {
	font-size: 24px;
}
.header-home .carousel-indicators button {
	width: 12px;
	height: 12px;
	background-color: #fff !important;
	border-radius: 50%;
}
.header-home .carousel-indicators button.active {
	background-color: #888;
}
.header-home .carousel-control-next,
.header-home .carousel-control-prev {
	display: none;
}

.scroll-animation div p {
	width: 100%;
	overflow: hidden;
}
.scroll-animation:hover div p {
	animation: scroll 5s infinite;
	animation-timing-function: ease-in-out;
}
@keyframes scroll {
	0% {
		translate: 0;
		overflow: visible;
	}
	100% {
		translate: -100%;
		overflow: visible;
	}
}
.rtl {
	direction: rtl;
}
.ltr {
	direction: ltr;
}
